@import url("https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
* {
  font-family: "Montserrat", "Cinzel Decorative";
}

::-webkit-scrollbar {
  width: 2px;
}

h1, h2, h3, h4, h5, h6, p, a {
  margin: 0px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "Cinzel Decorative";
}

p {
  font-family: "montserrat";
}

a {
  text-decoration: none;
}

.top-menu {
  width: 90%;
  height: 65px;
  display: flex;
  align-items: center;
  position: absolute;
  justify-content: space-between;
  top: 0px;
}
.top-menu h3 {
  color: #F2F2F2;
  font-family: "Cinzel Decorative";
  font-size: 1.5rem;
}
.top-menu svg {
  height: 29px;
  width: 46px;
}

h1 {
  font-size: 2rem;
}

h4 {
  font-size: 1.35rem;
}

p {
  font-size: 1.2rem;
}

.landing-banner {
  height: 98dvh;
  width: 100dvw;
  display: flex;
  justify-content: center;
}
.landing-banner .background {
  height: 100%;
  width: 100%;
  background-image: url(../src/images/products/landingbannerimage.jpg);
  background-size: 100%;
  background-repeat: no-repeat;
  filter: grayscale(100%) brightness(50%);
}

.brand-info-section {
  width: calc(100dvw - 80px);
  height: auto;
  display: grid;
  gap: 20px;
  margin: 40px;
  grid-template-areas: "a a a b b b b b b" "a a a b b b b b b" "a a a b b b b b b" "a a a c d d d d d" "e e e c d d d d d" "e e e c d d d d d";
}
.brand-info-section .a {
  grid-area: a;
  height: auto;
  max-height: 80vh;
  width: 35vw;
  max-width: 400px;
}
.brand-info-section .a img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: brightness(40%);
}
.brand-info-section .b {
  grid-area: b;
  min-height: 40vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 25px;
}
.brand-info-section .b p {
  max-width: 50vw;
}
.brand-info-section .c {
  grid-area: c;
  height: 100%;
  width: 100%;
}
.brand-info-section .d {
  grid-area: d;
  width: auto;
  height: 35vw;
  max-height: 400px;
}
.brand-info-section .d img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  filter: grayscale(100%);
}
.brand-info-section .e {
  grid-area: e;
  width: auto;
}

.landing-page-products {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.landing-page-products .product1,
.landing-page-products .product2 {
  height: 50vh;
  display: flex;
  align-items: center;
  padding: 20px 0px;
  margin: 0px 40px;
}
.landing-page-products .product1 .description,
.landing-page-products .product2 .description {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 12px;
  color: #F2F2F2;
  padding: 0px 40px;
}
.landing-page-products .product1 img,
.landing-page-products .product2 img {
  grid-column: span 3;
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.landing-page-products .product1 {
  background-color: #171717;
}
.landing-page-products .product1 .description {
  color: #F2F2F2;
}
.landing-page-products .product1 img {
  filter: brightness(60%);
}
.landing-page-products .product2 {
  background-color: #F2F2F2;
}
.landing-page-products .product2 .description {
  color: #373737;
}
.landing-page-products .product2 img {
  filter: grayscale(90%);
}

