// Import
@import url('https://fonts.googleapis.com/css2?family=Cinzel+Decorative:wght@400;700;900&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

// Variables
$primary-color: #000000;
$primary-color2: #F2F2F2;
$top-menu-height: 65px;

@mixin fdja {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
};
@mixin fdj {
    display: flex;
    flex-direction: column;
    justify-content: center;
};
@mixin fda {
    display: flex;
    flex-direction: column;
    align-items: center
};
@mixin fja {
    display: flex;
    justify-content: center;
    align-items: center;
};
@mixin fj {
    display: flex;
    justify-content: center;
};
@mixin fa {
    display: flex;
    align-items: center
};
@mixin fd{
    display: flex;
    flex-direction: column;
}

// General
*{
    font-family: 'Montserrat', "Cinzel Decorative";
}
::-webkit-scrollbar{
    width: 2px;
}
// .App {
//     text-align: center;
//     color: $primary-color;
//     background-color: $primary-color2;
// }
h1, h2, h3, h4, h5, h6, p, a{
    margin: 0px;
}
h1, h2, h3, h4, h5, h6{
    font-family: "Cinzel Decorative";
}
p{
    font-family: "montserrat";
}
a{
    text-decoration: none;
}

// Components
.top-menu{
    width: 90%;
    height: $top-menu-height;
    @include fa();
    position: absolute;
    justify-content: space-between;
    top: 0px;
    h3{
        color: $primary-color2;
        font-family: "Cinzel Decorative";
        font-size: 1.5rem;
    }
    svg{
        height: 29px;
        width: 46px;
    }
}

// Pages
// Landing Page
h1{
    font-size: 2rem;
}
h4{
    font-size: 1.35rem;
}
p{
    font-size: 1.2rem;
}
.landing-banner{
    height: 98dvh;
    width: 100dvw;
    @include fj;
    .background{
        height: 100%;
        width: 100%;
        background-image: url(../src/images/products/landingbannerimage.jpg);
        background-size: 100%;
        background-repeat: no-repeat;
        filter: grayscale(100%) brightness(50%);
    }
}
.brand-info-section{
    width: calc(100dvw - 80px);
    height: auto;
    display: grid;
    gap: 20px;
    margin: 40px;
    grid-template-areas: 
    "a a a b b b b b b"
    "a a a b b b b b b"
    "a a a b b b b b b"
    "a a a c d d d d d"
    "e e e c d d d d d"
    "e e e c d d d d d";
    .a{
        grid-area: a;
        height: auto;
        max-height: 80vh;
        width: 35vw;
        max-width: 400px;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            filter: brightness(40%);
        }
    }
    .b{
        grid-area: b;
        min-height: 40vh;
        width: 100%;
        @include fdj;
        gap: 25px;
        p{
            max-width: 50vw;
        }
    }
    .c{
        grid-area: c;
        height: 100%;
        width: 100%;
    }
    .d{
        grid-area: d;
        width: auto;
        height: 35vw;
        max-height: 400px;
        img{
            height: 100%;
            width: 100%;
            object-fit: cover;
            filter: grayscale(100%);
        }
    }
    .e{
        grid-area: e;
        width: auto;
    }
}
.landing-page-products{
    @include fda;
    .product1,
    .product2{
        height: 50vh;
        @include fa;
        padding: 20px 0px;
        margin: 0px 40px;
        .description{
            @include fd;
            align-items: flex-end;
            gap: 12px;
            color: $primary-color2;
            padding: 0px 40px;
        }
        img{
            grid-column: span 3;
            height: 100%;
            width: 100%;
            object-fit: cover;
        }
    }
    .product1{
        background-color: #171717;
        .description{
            color: #F2F2F2;
        }
        img{
            filter: brightness(60%);
        }
    }
    .product2{
        background-color: $primary-color2;
        .description{
            color: #373737;
        }
        img{
            filter: grayscale(90%);
        }
    }
}